.main-container-staff {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
}

.button-container {
  display: flex;
  flex-direction: row;
  gap: 35px;
  
}

.text-container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.inactiveText {
  display: none;
}

.activeText {
  display: block;
}

.active-button-staff, .button-staff{
  font-size: 27px;
  font-family: 'Alice', Courier;
  color: #aaaaaa;
  font-weight: lighter;
  cursor: pointer;
  border: none;
  text-decoration: none;
  position: relative;
  padding-bottom: 7px;
  letter-spacing: 2px;
  background-color: transparent;
}

.active-button-staff{
  color:rgb(255, 255, 255)
}

.button-staff:hover{
  color:rgb(255, 255, 255)
}



@media (max-width: 1050px) {
  .container {
    margin: 45px 0px;
  }

  .main-container-staff {
    margin-top: 90px;
  }
}

@media (max-width: 540px) {
  .container {
    gap: 25px;
  }

  .button-container {
    
    flex-direction: column;
    gap: 0px;
  }
}

@media (max-width: 450px) {
  .container {
    margin: 28px 0px;
    gap: 20px;
  }
}

@media (max-width: 390px) {
  .container {
    gap: 15px;
  }
}
