.main-container-about {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 20px;
  
}

.main-container-about h3 {
  margin: 0px;
  font-size: 28px;
  font-weight: 400;
}

.description-text-container h3 {
  color: #f7f7f7;
}

.synopsis-container h3 {
  color: var(--color-primario);;
}

.main-container-about p {
  margin: 0px;
  color: #aaaaaa;
  font-weight: 200;
  line-height: 1.5;
  font-size: 20px;
}

.synopsis-text-container p{
  text-align: right;
}

.description-container {
  display: flex;
  flex-direction: row;
  align-items: flex-end;
  gap: 20px;
}

.description-text-container {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-evenly;
  height: auto;
  width: 550px;
  box-sizing: border-box;
  padding: 1px 30px;
  gap: 20px;
}

.image-description img {
  max-width: 400px;
}

.image-synopsis img {
  max-width: 350px;
  margin-left: 30px;
}


.synopsis-container{
  display: flex;
  justify-content:space-between ;
  align-items: center;
  width: 100%;
  
}

.synopsis-text-container{
  width: 550px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 20px;
  justify-content: space-evenly;
  height: auto;
  box-sizing: border-box;
  padding: 1px 30px;
  gap: 20px;
}

@media (max-width: 1050px) {

  .main-container-about {
    margin-top: 80px;
  }

  .description-container {
    flex-direction: column;
  }

  .synopsis-container {
    flex-direction: column;
  }

  .image-description img {
    max-width: 500px;
  }

  .description-text-container {
    padding: 1px 15px;
  }

  .synopsis-text-container{
    max-width: 550px;
  }

  .image-synopsis img {
    display: none;
  }

}

@media (max-width: 540px) {
  .image-description img {
    max-width: 400px;
  }

  .synopsis-text-container {
    width: 400px;
    padding: 1px 5px;
  }

  .description-text-container {
    width: 400px;
    padding: 1px 5px;
  }
}

@media (max-width: 450px) {
  .image-description img {
    max-width: 350px;
  }

  .description-text-container {
    width: 350px;
  }

  .synopsis-text-container{
    width: 350px;
  }
}
